<template>
  <transition name="fade">
    <div class="poster-wrapper" v-if="isShow">
      <div class="poster-content">
        <div class="btn-close" @click="isShow = false"></div>
        <img src="../assets/poster.png" alt="poster" />
        <canvas
          class="qr-container"
          width="160px"
          height="160px"
          ref="canvas"
        />
        <div class="poster-btn-group">
          <button class="btn-poster btn-download" @click="download">
            Share Poster
          </button>
          <el-tooltip content="copy success!" :disabled="isDisabled">
            <button
              class="btn-poster btn-copy"
              v-clipboard:copy="inviteUrl"
              v-clipboard:success="onCopy"
            >
              Copy Link
            </button>
          </el-tooltip>
        </div>
      </div>
    </div>
  </transition>
</template>

<script>
import * as QRCode from "qrcode";

export default {
  name: "SharePoster",
  data() {
    return {
      isShow: false,
      inviteUrl: "",
      isDisabled: true,
    };
  },
  mounted() {},
  methods: {
    show() {
      this.isShow = true;
      this.$nextTick(() => {
        this.loadQR();
      });
    },
    loadQR() {
      QRCode.toCanvas(this.$refs.canvas, this.inviteUrl, {
        width: 160,
      })
        .then((url) => {
          console.log(url);
        })
        .catch((err) => {
          console.error(err);
        });
    },
    onCopy() {
      this.$message.success("Copied!");
    },
    download() {
      const canvas = document.createElement("canvas");
      const ctx = canvas.getContext("2d");
      const posterImg = this.$el.querySelector("img");
      const w = posterImg.width * 2;
      const h = posterImg.height * 2;
      canvas.width = w;
      canvas.height = h;

      function downloadImg(imgSrc, imgName) {
        let elem = document.createElement("a");
        elem.setAttribute("href", imgSrc);
        elem.setAttribute("download", imgName);

        const event = new MouseEvent("click");
        elem.dispatchEvent(event);
      }

      ctx.drawImage(posterImg, 0, 0, w, h);
      ctx.drawImage(this.$refs.canvas, 237 * 2, 356 * 2, 160, 160);

      canvas.toBlob(
        function (blob) {
          let imgSrc = window.URL.createObjectURL(blob);
          let imgName = "测试图片.jpg";
          downloadImg(imgSrc, imgName);
          window.URL.revokeObjectURL(imgSrc);
        },
        "image/jpeg",
        1
      );
    },
  },
};
</script>

