import Vue from "vue";
import SharePoster from "./Poster.vue";

const SharePosterDialog = Vue.extend(SharePoster);

SharePosterDialog.install = function (data) {
  let instance = new SharePosterDialog({
    data,
  }).$mount();

  document.body.appendChild(instance.$el);

  Vue.nextTick(() => {
    instance.show();
  });
};

export default SharePosterDialog; // 导出
